.App {
  text-align: center;
}

#quote-box {
  min-height: 130px;
  margin: 30px auto;
  padding: 10px;
  width: 350px;
  border-radius: 10px;
  position: relative;
  top: 20vh;
  -webkit-transition: 0.5s ease all;
  -o-transition: 0.5s ease all;
  transition: 0.5s ease all;
}

#text {
  color: white;
}

#author {
  text-align: right;
  padding: 10px;
  position: relative;
  right: 10px;
  bottom: 0px;
}

.App {
  -webkit-transition: 0.5s ease all;
  -o-transition: 0.5s ease all;
  transition: 0.5s ease all;
  height: 100vh;
}



